
const axios = require('axios').default;
import commonMiddleware from '../lib/commonMiddleware';

const capitalizeFirstLetter = (string) => {
  return string.charAt(0).toUpperCase() + string.slice(1);
};


const responseBuilder = (statusCode, data) => {
  let success;

  if (statusCode != 200 && statusCode != 201 && statusCode != 301) {
    success = false;
  } else {
    success = true;
  }

  let responseBody = {
    data: data,
    success: success,
  };

  let response = {
    statusCode: statusCode,
    headers: {
      my_header: 'my_value',
    },
    body: JSON.stringify(responseBody),
    isBase64Encoded: false,
  };

  return response;
};

/* POST Find Cities */
 async function findCities (event, context) {
   // This will prevent aws from closing the database connection pool
   context.callbackWaitsForEmptyEventLoop = false;

   console.log(event.body)

   let response;

   // Get keyword
   const { keyword } = event.body;

   // Capitalze keyword to get effective search
   const capitalizedKeyword = capitalizeFirstLetter(keyword);

   try {
     // Request parameters
     const options = {
       method: 'GET',
       url: 'https://spott.p.rapidapi.com/places',
       params: { q: capitalizedKeyword, limit: '100', type: 'CITY' },
       headers: {
         'x-rapidapi-key': '3aafaac323mshf2db844f4963925p12e224jsn1cee2c164576',
         'x-rapidapi-host': 'spott.p.rapidapi.com',
       },
     };

     // Request for all cities details
     const result = await axios.request(options);

     // Map city names alone without other details
     const cities = result.data.map((city) => city.name);

     // Set the response
     response = responseBuilder(200, cities);
     return response;
   } catch (error) {
     console.log(error);
     response = responseBuilder(400, error);
     return response;
   }
 };


 export const handler = commonMiddleware(findCities)